@tailwind base;
@tailwind components;
@tailwind utilities;

:root {
  --color-primary-orange-light: #fb923c;
  /* For gradients, loading buttons */
  --color-primary-orange: #f97316;
  /* Main orange for buttons, links, icons */
  --color-primary-orange-dark: #ea580c;
  /* Hover states, darker gradient */

  --color-text-dark: #1f2937;
  /* Main dark text */
  --color-text-medium: #6b7280;
  /* Secondary text, gray text */
  --color-text-light: #374151;
  /* Label text */
  --color-text-placeholder: #9ca3af;
  /* Input placeholder/icon */
  --color-text-white: white;
  /* White text */

  --color-error-red-light: #fef2f2;
  /* Error background */
  --color-error-red-border: #fecaca;
  /* Error border */
  --color-error-red-text: #ef4444;
  /* Error message text */
  --color-error-red-dark: #b91c1c;
  /* Darker error text */

  --color-border-gray: #d1d5db;
  /* General border */
  --color-bg-light-gray: #f9fafb;
  /* Light background */
  --color-bg-white: white;
  /* White background */

  /* Social Icon Colors */
  --color-social-facebook: #1877f2;
  --color-social-google: #ea4335;
  --color-social-apple: #000;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.inputfield {
  @apply w-full px-3 py-2 border border-gray-300 rounded-lg
}

.inputfield2 {
  @apply focus:outline-none focus:ring-2 focus:ring-orange-500 focus:border-transparent
}